@import "src/assets/styles/app";

.token {
  padding-top: 76px;

  .wrapper {
    border-top: 6px solid rgba(0, 0, 0, 0.26);
    border-bottom: 6px solid rgba(0, 0, 0, 0.26);
    background: #afd650;
    padding-top: 24px;


    .top {
      @include container;
      background: #bce756;

      .top_inner {
        @include inner;
        height: 80px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .top_inner_left {
          display: flex;

          .avatar {
            display: block;
            width: 41px;
            height: 43px;
          }

          .top_inner_left_info {
            margin-left: 14px;

            .info_top {
              display: flex;
              align-items: center;

              p {
                @include DMSans;
                font-weight: 400;
                font-size: 12px;
                color: #000;

                span {
                  color: #00529e;
                }
              }

              button {
                @include button;
                @include centerRow;
                margin-left: 7px;
                border-radius: 17px;
                width: 38px;
                height: 18px;
                background: #76a700;

                span {
                  @include DMSans;
                  font-weight: 400;
                  font-size: 12px;
                  text-align: center;
                  color: #afd650;
                }
              }
            }

            .info_address {
              margin-top: 7px;
              @include DMSans;
              font-weight: 400;
              font-size: 12px;
              color: #000;
            }

            .info_name {
              @include DMSans;
              font-weight: 400;
              font-size: 12px;
              color: #4e611f;
              margin-top: 4px;
            }
          }

        }

        .top_inner_right {

          .right_info {
            @include DMSans;
            font-weight: 400;
            font-size: 20px;
            color: rgba(0, 0, 0, 0.68);

            span {
              font-size: 12px;
            }
          }

          .right_socials {
            display: flex;
            justify-content: flex-end;
            align-items: center;

            a {
              text-decoration: none;

              &:nth-child(2) {
                margin-left: 2px;
              }

              &:nth-child(3) {
                margin-left: 5px;
              }

              &:nth-child(1) img {
                width: 29px;
                height: 29px;
              }

              &:nth-child(2) img {
                width: 19px;
                height: 19px;
              }

              &:nth-child(3) img {
                width: 23px;
                height: 19px;
              }

              img {
                display: block;
              }
            }
          }
        }
      }
    }

    .bottom {
      @include container;
      padding-bottom: 24px;

      .bottom_inner {
        @include inner;
        display: flex;
        justify-content: space-between;

        .bottom_left {
          padding-top: 14px;

          .left_top {
            display: flex;
            align-items: center;

            p {
              @include DMSans;
              font-weight: 400;
              font-size: 12px;
              color: #000;
            }

            svg {
              width: 20px;
              height: 20px;
              margin-left: 3px;
            }
          }

          .left_card {
            border-radius: 26px;
            background: #829f3b;
            padding: 8px 27px;
            width: 706px;
            margin-top: 6px;

            p {
              font-family: sans-serif;
              font-weight: 500;
              font-size: 12px;
              color: #000;

              &:nth-child(2),
              &:nth-child(3),
              &:nth-child(4),
              &:nth-child(5),
              &:nth-child(8),
              &:nth-child(12),
              &:nth-child(13),
              &:nth-child(14),
              &:nth-child(15),
              &:nth-child(18)
              {
                padding-left: 28px;
              }

              &:nth-child(9),
              &:nth-child(10),
              &:nth-child(11),
              &:nth-child(16),
              &:nth-child(17)
              {
                padding-left: 56px;
              }
            }
          }


        }

        .bottom_right {
          margin-left: 53px;
          flex-grow: 1;
          padding-top: 40px;

          .row {
            padding-bottom: 10px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.31);
            display: flex;

            &:not(:first-child) {
              margin-top: 10px;
            }

            .label, .value {
              @include DMSans;
              font-weight: 400;
              font-size: 12px;
              color: #000;
            }

            .label {
              width: 184px;
            }

          }

        }
      }
    }


  }
}