@import "src/assets/styles/app";

.whyFamilyPepe {
  @include container;
  padding-top: 62px;

  .inner {
    @include inner;
    height: 697px;
    position: relative;
    padding-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .back {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 72px;
      border: 6px solid rgba(0, 0, 0, 0.35);
    }

    .title {
      @include h2;
      position: relative;
      z-index: 10;

    }

    .text {
      position: relative;
      z-index: 10;
      margin-top: 94px;
      border: 3px solid rgba(0, 0, 0, 0.44);
      border-radius: 29px;
      width: 529px;
      padding: 16px;
      backdrop-filter: blur(7.599999904632568px);
      background: rgba(163, 195, 74, 0.65);

      p {
        @include DigitalStrip2BB_Reg;
        font-weight: 700;
        font-size: 27px;
        line-height: 118%;
        letter-spacing: 0.04em;
        text-align: center;
        color: #000;
      }
    }

    .left0 {
      position: absolute;
      width: auto;
      height: 378px;
      left: -65px;
      bottom: 0;
    }

    .left1 {
      position: absolute;
      width: auto;
      height: 456px;
      left: 110px;
      bottom: 106px;
    }

    .center {
      position: absolute;
      width: auto;
      height: 338px;
      left: 428px;
      bottom: -45px;
    }

    .right0 {
      position: absolute;
      width: auto;
      height: 454px;
      right: 150px;
      bottom: 140px;
    }

    .right1 {
      position: absolute;
      width: auto;
      height: 382px;
      right: 0px;
      bottom: 10px;
    }


  }
}