//========= BREAK POINTS ===========//
@mixin desktop {
  @media all and (min-width: 1440px) {
    @content;
  }
}

@mixin onlyMobile($displayType: block) {
  display: $displayType;
  @content;

  @include desktop {
    display: none;
  }
}


@mixin onlyDesktop($displayType: block) {
  display: none;

  @include desktop {
    display: $displayType;
    @content;
  }
}

//========= FONTS ===========//
@mixin Bangers {
  font-family: "Bangers", system-ui;
  font-weight: 400;
  font-style: normal;
}

@mixin FamilyGuy {
  font-family: "Family Guy", sans-serif;
  font-weight: 400;
  font-style: normal;
}

@mixin DigitalStrip2BB_Bold {
  font-family: "DigitalStrip2BB_bold", sans-serif;
}

@mixin DigitalStrip2BB_Reg {
  font-family: "DigitalStrip2BB_reg", sans-serif;
}

@mixin DMSans {
  font-family: "DM Sans", sans-serif;
  font-style: normal;
}

@mixin h2 {
  @include FamilyGuy;
  @include border_black_4;
  font-weight: 400;
  font-size: 64px;
  letter-spacing: 0.04em;
  text-align: center;
  color: #30ee00;
}


//========= CONTAINERS ===========//
@mixin container {
  padding-left: 24px;
  padding-right: 24px;

  @include desktop {
    padding-left: 0;
    padding-right: 0;
  }
}

@mixin inner {
  @include desktop {
    width: 1340px;
    margin-left: auto;
    margin-right: auto;
  }
}

//========= OTHER ===========//
@mixin centerColumn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@mixin centerRow {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

@mixin absoluteCenter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

