@import "src/assets/styles/app";

.family {
  @include container;
  padding-top: 16px;

  .inner {
    @include inner;
    position: relative;
    height: 697px;
    border: 7px solid rgba(0, 0, 0, 0.42);
    border-radius: 72px;
    padding-top: 55px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .back {
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 72px;
      filter: blur(5.3px);
    }

    .left0 {
      position: absolute;
      width: auto;
      height: 420px;
      left: -30px;
      bottom: 0;
    }

    .left1 {
      position: absolute;
      width: auto;
      height: 386px;
      left: 118px;
      bottom: 0;
    }

    .left2 {
      position: absolute;
      width: auto;
      height: 294px;
      left: 297px;
      bottom: -17px;
    }

    .right0 {
      position: absolute;
      width: auto;
      height: 272px;
      right: 270px;
      bottom: 0px;
    }

    .right1 {
      position: absolute;
      width: auto;
      height: 360px;
      right: 115px;
      bottom: 0px;
    }

    .right2 {
      position: absolute;
      width: auto;
      height: 394px;
      right: -31px;
      bottom: 16px;
      transform: rotateY(180deg);
    }

    .title {
      position: relative;
      z-index: 10;
      @include FamilyGuy;
      @include border_black_7;
      font-weight: 400;
      font-size: 156px;
      letter-spacing: 0.03em;
      text-align: center;
      color: #30ee00;
      width: 659px;

    }

    .subtitle {
      margin-top: 30px;
      @include Bangers;
      @include border_darkgreen_4;
      position: relative;
      z-index: 10;
      font-weight: 400;
      font-size: 66px;
      letter-spacing: 0.03em;
      text-align: center;
      color: #9ec44f;
    }

    .watchVideo {
      margin-top: 76px;
      position: relative;
      z-index: 10;
      @include centerRow;
      text-decoration: none;
      border-radius: 14px;
      box-shadow: 0 0 0 2px rgba(61, 112, 201, 0.4), inset 0 2px 1px 0 rgba(0, 0, 0, 0.25);
      background: #57761e;
      padding: 2px 3px;

      &:not(:first-child) {
        margin-left: 30px;
      }

      .socialLink_wrapper {
        @include centerRow;
        border-radius: 12px;
        background: linear-gradient(180deg, #b0d44f 0%, #628f30 20.83%, #365914 94.27%);
        padding: 7px 10px;
      }

      .socialLink_inner {
        @include centerRow;
        border-radius: 8px;
        width: 274px;
        height: 56px;
        box-shadow: inset 0 1px 3px 0 rgba(255, 255, 255, 0.25);
        background: linear-gradient(180deg, #a1ca3d 0%, #5c8526 100%);

        span {
          @include FamilyGuy;
          @include border_darkgreen_1;
          font-weight: 400;
          font-size: 26px;
          letter-spacing: 0.04em;
          color: #faf5cd;
        }
      }
    }

  }
}