@import "src/assets/styles/app";

.join {
  position: relative;
  height: (3.066 / 4.32) * 100vw;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .back {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100vw;
    height: auto;
  }

  .gifWrapper {
    position: relative;
    margin-top: (280 / 1154) * 100vw;
    width: (260 / 1154) * 100vw;
    height: (170 / 1154) * 100vw;
    //border: 1px solid red;
    border-radius: 0.5 * (170 / 1154) * 100vw;
    overflow: hidden;

    img {
      position: absolute;
      top: (4 / 1154) * 100vw;
      left: (12 / 1154) * 100vw;
      width: (230 / 1154) * 100vw;
      height: auto;
    }

  }

  .text {
    padding: 24px;
    margin-top: 62px;
    border: 5px solid rgba(0, 0, 0, 0.45);
    border-radius: 32px;
    backdrop-filter: blur(15px);
    background: rgba(155, 213, 255, 0.72);
    width: 636px;

    p {
      @include DigitalStrip2BB_Reg;
      font-weight: 700;
      font-size: 47px;
      letter-spacing: 0.04em;
      text-align: center;
      color: #000;

      &:nth-child(2) {
        margin-top: 47px;
      }

      span {
        text-decoration: underline;
      }
    }
  }

}