@import "src/assets/styles/app";

.items {
  @include container;
  padding-top: 40px;

  .inner {
    @include inner;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 60px 24px;

    .item {
      height: 294px;
      border: 5px solid rgba(0, 0, 0, 0.45);
      border-radius: 32px;
      padding: 45px 50px 0;

      .item_title {
        @include DigitalStrip2BB_Reg;
        font-weight: 700;
        font-size: 30px;
        line-height: 118%;
        letter-spacing: 0.04em;
        color: #000;
      }

      .item_text {
        @include DigitalStrip2BB_Reg;
        font-weight: 700;
        font-size: 25px;
        line-height: 118%;
        letter-spacing: 0.04em;
        color: #000;
        margin-top: 24px;
      }
    }


  }
}