@import url('https://fonts.googleapis.com/css2?family=Bangers&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: "Family Guy";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Family Guy"),
  url(./assets/fonts/FamilyGuy.ttf) format("truetype");
}

@font-face {
  font-family: "DigitalStrip2BB_bold";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("DigitalStrip2BB_bold"),
  url(./assets/fonts/DigitalStrip2BB_bold.ttf) format("truetype");
}

@font-face {
  font-family: "DigitalStrip2BB_reg";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("DigitalStrip2BB_ref"),
  url(./assets/fonts/DigitalStrip2BB_reg.ttf) format("truetype");
}