@import "src/assets/styles/app";

.header {
  @include container;
  padding-top: 64px;

  .inner {
    @include inner;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      @include Bangers;
      @include border_green_6;
      text-decoration: none;
      font-weight: 400;
      font-size: 106px;
      letter-spacing: 0.04em;
      text-align: center;
      color: #9ec44f;
    }

    .socialLinks {
      display: flex;

      .socialLink {
        @include centerRow;
        text-decoration: none;
        border-radius: 14px;
        //width: 223px;
        //height: 74px;
        box-shadow: 0 0 0 2px rgba(61, 112, 201, 0.4), inset 0 2px 1px 0 rgba(0, 0, 0, 0.25);
        background: #57761e;
        padding: 2px 3px;

        &:not(:first-child) {
          margin-left: 30px;
        }

        .socialLink_wrapper {
          @include centerRow;
          border-radius: 12px;
          //width: 217px;
          //height: 70px;
          background: linear-gradient(180deg, #b0d44f 0%, #628f30 20.83%, #365914 94.27%);
          padding: 7px 10px;
        }

        .socialLink_inner {
          @include centerRow;
          border-radius: 8px;
          width: 197px;
          height: 56px;
          box-shadow: inset 0 1px 3px 0 rgba(255, 255, 255, 0.25);
          background: linear-gradient(180deg, #a1ca3d 0%, #5c8526 100%);


          span {
            @include FamilyGuy;
            @include border_darkgreen_1;
            font-weight: 400;
            font-size: 26px;
            letter-spacing: 0.04em;
            color: #faf5cd;
            //text-shadow: 0 1px #3c5511;
          }
        }
      }
    }

  }
}