@import "src/assets/styles/app";

.roadmap {
  @include container;
  padding-top: 56px;

  .inner {
    @include inner;

    .title {
      @include h2;
    }

    .cards {
      margin-top: 26px;

      .card {
        border: 6px solid rgba(0, 0, 0, 0.35);
        border-radius: 27px;
        overflow: hidden;
        height: 358px;
        display: flex;

        &:not(:first-child) {
          margin-top: 26px;
        }

        &:nth-child(odd) {
          flex-direction: row;
        }

        &:nth-child(even) {
          flex-direction: row-reverse;
        }

        .gifWrapper {
          width: 688px;
          flex-shrink: 0;
          height: 100%;
        }

        .texts {
          flex-grow: 1;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          padding-left: 32px;

          p {
            @include DigitalStrip2BB_Reg;
            font-weight: 700;
            font-size: 30px;
            line-height: 118%;
            letter-spacing: 0.04em;
            color: #000;
          }

          .stages {
            margin-top: 24px;
          }
        }
      }


    }
  }
}