@import "src/assets/styles/app";

.footer {
  @include container;
  padding-top: 58px;
  padding-bottom: 194px;
  background: #847F69;


  .socialLinks {
    display: flex;
    justify-content: center;

    .socialLink {
      @include centerRow;
      text-decoration: none;
      border-radius: 14px;
      //width: 223px;
      //height: 74px;
      box-shadow: 0 0 0 2px rgba(61, 112, 201, 0.4), inset 0 2px 1px 0 rgba(0, 0, 0, 0.25);
      background: #57761e;
      padding: 2px 3px;

      &:not(:first-child) {
        margin-left: 70px;
      }

      .socialLink_wrapper {
        @include centerRow;
        border-radius: 12px;
        //width: 217px;
        //height: 70px;
        background: linear-gradient(180deg, #b0d44f 0%, #628f30 20.83%, #365914 94.27%);
        padding: 7px 10px;
      }

      .socialLink_inner {
        @include centerRow;
        border-radius: 8px;
        width: 197px;
        height: 56px;
        box-shadow: inset 0 1px 3px 0 rgba(255, 255, 255, 0.25);
        background: linear-gradient(180deg, #a1ca3d 0%, #5c8526 100%);


        span {
          @include FamilyGuy;
          @include border_darkgreen_1;
          font-weight: 400;
          font-size: 26px;
          letter-spacing: 0.04em;
          color: #faf5cd;
          //text-shadow: 0 1px #3c5511;
        }
      }
    }
  }

  .address {
    margin-top: 32px;
    @include FamilyGuy;
    font-weight: 400;
    font-size: 30px;
    letter-spacing: 0.04em;
    text-align: center;
    background: linear-gradient(180deg, #30ee00 0%, #dbff00 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}